import React from 'react'
import Link from 'gatsby-link'

import { head } from '../content/home'
import Head from '../components/Head'
import Socials from '../components/Socials'
import Container from '../components/Container'
import FadeInUp from '../components/FadeInUp'

const Callout = props => (
  <Link className='sans-serif link fw9 black hover-gold' {...props} />
)

const HomePage = () => (
  <Container>
    <Head {...head} />
    <FadeInUp>
      <section className='pv5 center measure georgia'>
        <h1 className='sans-serif'>Tim Shi</h1>
        <hr />
        <p>
          Currently, I am a cofounding <a href="http://cresta.ai" target="_blank">Cresta</a>, an enterprise AI company that augments humans to do less repetitive work. 
        </p>

        <p>
          Previously, I was a PhD student at Stanford AI lab with <a href="https://cs.stanford.edu/~pliang/">Percy Liang</a>. I am enthusiastic about building natural language interface between human and computers. 
        </p>

        <p>
          I worked at OpenAI on World of Bits.
        </p>
        <Socials />
      </section>
    </FadeInUp>
  </Container>
)

export default HomePage



